import { reactive, ref, watch, toRaw, toRefs } from 'vue';
export default {
  props: {
    formItems: {
      type: Array,
      default: () => {// 
      }
    },
    modelValue: {
      type: Object,
      require: true
    }
  },

  data() {
    return {
      formData: {
        name: "ss"
      }
    };
  },

  emits: ["update:modelValue"],

  setup(props, {
    emit
  }) {
    const formData = ref({ ...props.modelValue
    });
    watch(formData, newValue => {
      emit('update:modelValue', newValue);
    }, {
      deep: true
    });
    return {
      formData: formData
    };
  }

};